import React, {useCallback, useEffect, useState} from "react"
import { Link, graphql } from "gatsby"

import Bio from "../components/bio"
import Layout from "../components/layout"
import Seo from "../components/seo"

const BlogIndex = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata?.title || `Title`
  const allPosts = data.allMarkdownRemark.nodes
  const categories = [{fieldValue: '전체보기', totalCount: allPosts.length}, ...data.categories.group]

  const [posts, setPosts] = useState([...allPosts]);
  const [curCategory, setCurCategory] = useState('전체보기');

  const onClickCategory = useCallback((e)=> {
    setCurCategory(e.target.value);
  }, []);

  useEffect(() => {
    if (curCategory === '전체보기')
      setPosts([...allPosts])
    else
      setPosts([...allPosts.filter((post)=>post.frontmatter.category === curCategory)])
  }, [curCategory]);

  return (
    <Layout location={location} title={siteTitle}>
      <Bio />
      {posts.length} 개의 글
      <div className="tag-wrapper">
        {categories.map(category => (
          <button
            className="tag-button"
            key={category.fieldValue}
            onClick={onClickCategory}
            value={category.fieldValue}
          >
            {category.fieldValue}
          </button>
        ))}
      </div>
      <ol style={{ listStyle: `none` }}>
        {posts.map(post => {
          const title = post.frontmatter.title || post.fields.slug

          return (
            <li key={post.fields.slug}>
              <article
                className="post-list-item"
                itemScope
                itemType="http://schema.org/Article"
              >
                <header>
                  <h2>
                    <Link to={post.fields.slug} itemProp="url">
                      <span itemProp="headline">{title}</span>
                    </Link>
                  </h2>
                  <small>{post.frontmatter.date}</small>
                </header>
                <section>
                  <p
                    dangerouslySetInnerHTML={{
                      __html: post.frontmatter.description || post.excerpt,
                    }}
                    itemProp="description"
                  />
                </section>
              </article>
            </li>
          )
        })}
      </ol>
    </Layout>
  )
}

export default BlogIndex

/**
 * Head export to define metadata for the page
 *
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head = () => <Seo title="Huiy's Blog" />

export const pageQuery = graphql`
  {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { frontmatter: { date: DESC } }) {
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
          category
        }
      }
    }
    categories: allMarkdownRemark(limit: 2000) {
      group(field: frontmatter___category) {
        fieldValue
        totalCount
      }
    }
  }
`